<template>
	<div id="app" class="full-screen">
		<transition enter-active-class="animated fadeInRight" leave-active-class="animated fadeOutRight">
			<router-view></router-view>
		</transition>
		<transition enter-active-class="animated" leave-active-class="animated fadeOutDown">
			<div v-show="!isLogin" class="full-screen">
				<div class="aui-content aui-margin-b-15" style="margin-top: 100px;">
					<ul class="aui-list aui-form-list">
						<li class="aui-list-item">
							<div class="aui-list-item-inner">
								<div class="aui-list-item-label">
									
								</div>
								<div class="aui-list-item-input">
									<input v-model="username" type="text" placeholder="Name">
								</div>
							</div>
						</li>
						<li class="aui-list-item">
							<div class="aui-list-item-inner">
								<div class="aui-list-item-label">
									
								</div>
								<div class="aui-list-item-input">
									<input v-model="password" type="password" placeholder="Password">
								</div>
							</div>
						</li>
					</ul>
				</div>

				<div @click="loginAction" class="aui-btn aui-btn-primary aui-btn-block aui-btn-sm" style="margin-bottom: 10px;"></div>
				<div @click="registAction" class="aui-btn aui-btn-primary aui-btn-block aui-btn-sm"></div>
			</div>
		</transition>
		<header class="aui-bar aui-bar-nav">{{username}}</header>
		<div v-if="isOffLine" class="aui-tips aui-margin-b-15" id="tips-1" style="background-color: rgba(229,28,35,.6);">
			<i class="aui-iconfont aui-icon-info"></i>
			<div class="aui-tips-title"></div>
			<i class="aui-iconfont aui-icon-close"></i>
		</div>
		<div class="aui-content app-body">
			<div class="aui-content aui-hide" :class="{'aui-show':showItem==0}">
				<ul class="aui-list aui-media-list">
					<li @click="openChatView(item)" v-for="(item,i) in friends" class="aui-list-item aui-list-item-middle">
						<div class="aui-media-list-item-inner">
							<div class="aui-list-item-media wechat-avatar">
								<div v-if="item.msgNum>0" class="aui-badge">{{item.msgNum}}</div>
								<img src="http://i1.piimg.com/1949/6f7764a45a1d1a50.png">
							</div>
							<div class="aui-list-item-inner">
								<div class="aui-list-item-text">
									<div class="aui-list-item-title">{{item.name}}</div>
									<div class="aui-list-item-right"></div>
								</div>
								<div class="aui-list-item-text aui-font-size-12">
									{{item.id}}
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<div class="aui-content aui-hide" :class="{'aui-show':showItem==1}">
				<ul @click="openChatView(item)" v-for="(item,i) in groups" class="aui-list aui-media-list">
					<li class="aui-list-item aui-list-item-middle wechat-top">
						<div class="aui-media-list-item-inner">
							<div class="aui-list-item-media wechat-avatar">
								<div v-if="item.msgNum>0" class="aui-badge">{{item.msgNum}}</div>
								<div class="aui-row-padded">
									<div class="aui-col-xs-6">
										<img src="http://i1.piimg.com/1949/6f7764a45a1d1a50.png" />
									</div>
									<div class="aui-col-xs-6">
										<img src="http://i1.piimg.com/1949/6f7764a45a1d1a50.png" />
									</div>
									<div class="aui-col-xs-6">
										<img src="http://i1.piimg.com/1949/6f7764a45a1d1a50.png" />
									</div>
									<div class="aui-col-xs-6">
										<img src="http://i1.piimg.com/1949/6f7764a45a1d1a50.png" />
									</div>
								</div>

							</div>
							<div class="aui-list-item-inner">
								<div class="aui-list-item-text">
									<div class="aui-list-item-title">{{item.name}}</div>
									<div class="aui-list-item-right"></div>
								</div>
								<div class="aui-list-item-text aui-font-size-12">
									{{item.id}}
								</div>
							</div>
						</div>
					</li>
				</ul>
			</div>
			<div class="aui-content aui-hide" :class="{'aui-show':showItem==2}">
				<ul class="aui-list aui-list-in">

					<li @click="openSearchView" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-home"></i>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
					<li @click="openNoticeView" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-edit"></i>
							<div v-show="subscribesUnreadNum" class="aui-dot"></div>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
					<li @click="addFriendsAction" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-camera"></i>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
					<li @click="openCreatGroupView" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-camera"></i>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
					<li @click="refreshFriendAndGroupListAction" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-camera"></i>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
					<li @click="closeChat" class="aui-list-item">
						<div class="aui-list-item-label-icon">
							<i class="aui-iconfont aui-icon-camera"></i>
						</div>
						<div class="aui-list-item-inner">
							
						</div>
					</li>
				</ul>
			</div>
		</div>

		<footer class="aui-bar aui-bar-tab" id="footer">
			<div class="aui-bar-tab-item" :class="{'aui-active':showItem===0}" @click="showItem=0" tapmode>
				<div v-show="friendsUnReadNum" class="aui-badge">{{friendsUnReadNum}}</div>
				<i class="aui-iconfont aui-icon-my"></i>
				<div class="aui-bar-tab-label"></div>
			</div>
			<div class="aui-bar-tab-item" :class="{'aui-active':showItem===1}" @click="showItem=1" tapmode>
				<div v-show="groupsUnReadNum" class="aui-badge">{{groupsUnReadNum}}</div>
				<i class="aui-iconfont aui-icon-comment"></i>
				<div class="aui-bar-tab-label"></div>
			</div>
			<div class="aui-bar-tab-item" :class="{'aui-active':showItem===2}" @click="showItem=2" tapmode>
				<div v-show="subscribesUnreadNum" class="aui-dot"></div>
				<!--<div class="aui-badge">99</div>-->
				<i class="aui-iconfont aui-icon-paper"></i>
				<div class="aui-bar-tab-label"></div>
			</div>
		</footer>

	</div>
</template>

<script>
	export default {
		data() {
			return {
				isOffLine: false,
				//
				showItem: 0,
				//
				userInfo: {},
				isLogin: false,
				username: '',
				password: '',
				//
				friendChatArr: [],
				//
				groupChatArr: [],
				//
				subscribes: [],
				//
				friends: [],
				//
				groups: [],
				friendsUnReadNum: 0,
				groupsUnReadNum: 0,
				subscribesUnreadNum: 0,

			};
		},
		watch: {
			//     
			friendChatArr: function(arr, oldArr) {
				console.log(' ');
				App.$emit('onChatMsgSuccess', arr);
				App.computUnReadMsgNum();
			},
			//      
			groupChatArr: function(arr, oldArr) {
				console.log(' ');
				App.$emit('onChatMsgSuccess', arr);
				App.computUnReadMsgNum();
			},
			//
			subscribes: function(arr, oldArr) {
				console.log(' ');
				App.computUnReadSubscribes();
			},
		},
		methods: {
			/**
			 * 
			 */
			//
			onOnline() {
				console.warn('');
				this.isOffLine = false;
			},
			//
			onOffline() {
				console.warn('');
				this.isOffLine = true;
				dialog.alert({
					title: '',
					msg: '',
					buttons: [''],
				});
			},
			//
			onTextMessage(message) {
				var self = this;
				var res_msg = {
					data: message.data,
					date: new Date().getTime(),
					id: message.id,
					from: message.from,
					to: message.to,
					is_mine: false,
					is_read: false,
					msg_type: 'text',
					chat_type: '',
				};
				if(message.type == 'chat') {
					res_msg.chat_type = 'single_chat';
					App.friendChatArr.push(res_msg);
				} else if(message.type == 'groupchat') {
					res_msg.chat_type = 'group_chat';
					App.groupChatArr.push(res_msg);
				}
				//
				//dbManager.saveChat(res_msg);
			},
			//
			onPictureMessage(message) {
				var self = this;
				var res_msg = {
					data: message.url, //
					date: new Date().getTime(),
					id: message.id,
					from: message.from,
					to: message.to,
					is_mine: false,
					is_read: false,
					msg_type: 'image', //
					chat_type: '',
				};
				if(message.type == 'chat') {
					res_msg.chat_type = 'single_chat';
					App.friendChatArr.push(res_msg);
				} else if(message.type == 'groupchat') {
					res_msg.chat_type = 'group_chat';
					App.groupChatArr.push(res_msg);
				}
				//
				//dbManager.saveChat(res_msg);
			},
			// 
			onPresenceTypeSubscribe(message) {
				/*{
				 chatroom: false,
				 code: null,
				 from: "test1",
				 fromJid: "zj1406187962#layimtest_test1@easemob.com",
				 original_type: "subscribe",
				 presence_type: "",
				 status: "",
				 to: "test4",
				 toJid: "zj1406187962#layimtest_test4@easemob.com",
				 type: "subscribe",
				}*/
				message.isRead = false;
				App.subscribes.push(message);
			},
			/**
			 * APP
			 */
			//
			judgeIsLogin() {
				var self = this;
				var user = localStorage.getItem('user');
				var pwd = localStorage.getItem('pwd');
				if(user && pwd) {
					self.username = user;
					self.password = pwd;
				}
			},
			//
			registAction() {
				var self = this;
				self.$router.push({
					name: 'Regist',
				});
			},
			//
			loginAction(username, password) {
				toast.loading({
					title: "",
				});
				var self = this;
				self.friendChatArr = [];
				self.groupChatArr = [];
				self.subscribes = [];
				console.log(' ');
				httpTool.login({
					user: self.username,
					pwd: self.password,
					success: function(userInfo) {
						console.log('%o', userInfo)
						setTimeout(function() {
							toast.hide();
							self.loginActionSuccess(userInfo);
						}, 1000);
					},
					error: function(err) {
						toast.hide();
						dialog.alert({
							title: '',
							msg: '',
							buttons: ['']
						});

					}
				});
			},
			//
			loginActionSuccess(userInfo) {
				var self = this;
				self.showItem = 0;
				//dbManager.createTable(self.username);
				//dbManager.setUserInfo(userInfo);
				localStorage.setItem('user', self.username);
				localStorage.setItem('pwd', self.password);
				var self = this;
				console.log('');
				self.isLogin = true;
				// //
				self.refreshFriendAndGroupListAction()
			},
			//
			registCilck(username, password) {
				var self = this;
				httpTool.registerUser({
					user: username,
					pwd: password,
					success: function() {
						toast.success({
							title: "",
							duration: 2000
						});

					},
					error: function() {
						toast.fail({
							title: "",
							duration: 2000
						});

					},

				});
			},
			//
			openChatView(friend) {
				console.log('');
				var self = this;
				self.$router.push({
					name: 'Chat',
					params: friend
				});
			},
			//
			openSearchView() {
				var self = this;
				self.$router.push({
					name: 'Search'
				});
			},
			//
			openNoticeView() {
				var self = this;
				self.$router.push({
					name: 'Notice'
				});
			},
			//
			addFriendsAction() {
				var self = this;
				self.$router.push({
					name: 'AddFriend'
				});
			},
			// 
			openCreatGroupView() {
				var self = this;
				self.$router.push({
					name: 'CreateGroup'
				});
			},
			//
			refreshFriendAndGroupListAction() {
				var self = this;
				// //
				toast.loading({
					title: "",
				});
				httpTool.getRoster(function(arr) {
					console.log(':%o', arr);
					self.friends = arr;
					httpTool.getListGroups(function(arr) {
						console.log(':%o', arr);
						self.groups = arr;
						toast.hide();
						self.computUnReadMsgNum();
					});
				});

			},
			//  
			computUnReadMsgNum() {
				var self = this;
				self.friendsUnReadNum = 0;
				self.groupsUnReadNum = 0;
				// 
				$.each(self.friends, function(i, friend) {
					var num = 0;
					$.each(App.friendChatArr, function(j, msg) {
						if(msg.from == friend.id && msg.is_read == false) {
							num++;
							self.friendsUnReadNum++;
						}
					});
					friend.msgNum = num;
				});
				// 
				$.each(self.groups, function(i, group) {
					var num = 0;
					$.each(App.groupChatArr, function(j, msg) {
						if(msg.to == group.id && msg.is_read == false) {
							num++;
							self.groupsUnReadNum++;
						}
					});
					group.msgNum = num;
				});
			},
			//
			computUnReadSubscribes() {
				var self = this;
				self.subscribesUnreadNum = 0;
				$.each(self.subscribes, function(i, msg) {
					if(msg.isRead == false) {
						self.subscribesUnreadNum++;
					}
				});
			},
			closeChat() {
				conn.close();
				this.isLogin = false;
			},
		},
		mounted() {
			App = this;
			//
			App.judgeIsLogin();
		},
	}
</script>

<style>
	.aui-content.app-body {
		bottom: 53px;
	}
	
	.aui-list-item.wechat-top {
		background-color: #f3f3f7;
	}
	
	.aui-list .wechat-avatar {
		width: 3rem;
	}
	
	.wechat-avatar .aui-row-padded {
		background-color: #dddee0;
		padding: 0.1rem;
		border-radius: 0.2rem;
		margin-left: -0.05rem;
		margin-right: -0.05rem;
	}
	
	.wechat-avatar .aui-row-padded [class*=aui-col-xs-] {
		padding: 0.05rem;
	}
	
	.wechat-avatar,
	.wechat-avatar>img {
		border-radius: 0.2rem;
	}
</style>


