<template>
	<div class="full-screen" style="z-index: 111;">
		<header class="aui-bar aui-bar-nav">
			<a @click="backAction" class="aui-pull-left aui-btn">
				<span class="aui-iconfont aui-icon-left"></span>
			</a>
			<div class="aui-title"></div>
		</header>
		<div class="aui-content">
			<ul class="aui-list aui-form-list">
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							ID
						</div>
						<div class="aui-list-item-input">
							<input v-model="username" type="text" placeholder="" />
						</div>
					</div>
				</li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							
						</div>
						<div class="aui-list-item-input">
							<input v-model="nickname" type="text" placeholder="()" />
						</div>
					</div>
				</li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							
						</div>
						<div class="aui-list-item-input">
							<input v-model="password" type="password" placeholder="" />
						</div>
					</div>
				</li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							
						</div>
						<div class="aui-list-item-input">
							<input v-model="password2" type="password" placeholder="" />
						</div>
					</div>
				</li>
			</ul>
			<br />
			<div @click="registAction" class="aui-btn aui-btn-block aui-btn-primary aui-btn-sm">
				
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				username: '',
				nickname: '',
				password: '',
				password2: '',
			};
		},
		methods: {
			backAction() {
				this.$router.back();
			},
			registAction() {
				var self = this;
				if(self.username === '') {
					dialog.alert({
						title: '',
						msg: 'ID',
						buttons: [''],
					});
					return;
				}
				if(self.password != self.password2) {
					dialog.alert({
						title: '',
						msg: '2',
						buttons: [''],
					});
					return;
				}
				var options = {
					username: self.username,
					password: self.password,
					nickname: self.nickname,
					appKey: WebIM.config.appkey,
					success: function() {
						dialog.alert({
							title: '',
							msg: '',
							buttons: [''],
						}, function() {
							App.username = self.username;
							App.password = self.password;
							self.backAction();
						});
					},
					error: function(err) {
						err = JSON.parse(err.data);
						var errMsg = '';
						if(err.error === 'illegal_argument') {
							errMsg = '!';
						} else if(err.error === 'duplicate_unique_property_exists') {
							errMsg = 'ID!';
						} else {
							errMsg = err.error;
						}
						dialog.alert({
							title: '',
							msg: errMsg,
							buttons: [''],
						});
						console.warn(JSON.stringify(err));
					},
					apiUrl: WebIM.config.apiURL
				};
				conn.registerUser(options);
			},
		},
	}
</script>

<style>

</style>


