<template>
	<div class="full-screen">
		<header class="aui-bar aui-bar-nav">
			<a @click="backAction" class="aui-pull-left aui-btn">
				<span class="aui-iconfont aui-icon-left"></span>
			</a>
			<div class="aui-title"></div>
		</header>
		<div class="aui-content">
			<ul class="aui-list aui-form-list">
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							
						</div>
						<div class="aui-list-item-input">
							<input v-model="subject" type="text" placeholder="">
						</div>
					</div>
				</li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<div class="aui-list-item-label">
							
						</div>
						<div class="aui-list-item-input">
							<textarea v-model="description" placeholder=""></textarea>
						</div>
					</div>
				</li>
				<li class="aui-list-header"></li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<label><input v-model="optionsPublic" class="aui-checkbox" type="checkbox" name="checkbox"> </label>
					</div>
				</li>
				<li class="aui-list-item">
					<div class="aui-list-item-inner">
						<label><input v-model="optionsAllowInvites" class="aui-checkbox" type="checkbox" name="checkbox"> </label>
					</div>
				</li>

			</ul>
			<div @click="createGroupAction" class="aui-btn aui-btn-info aui-btn-block aui-btn-sm" style="margin: 10px;width: 95%"></div>
		</div>
	</div>

</template>

<script>
	export default {
		data() {
			return {
				subject: '', // 
				description: '', // 
				members: [], // ID
				optionsPublic: true, // 
				optionsModerate: false, // 
				optionsMembersOnly: false, // 
				optionsAllowInvites: true, // 

				/*  */
				friends: App.friends,
			};
		},
		methods: {
            backAction() {
				this.$router.back();
			},
			//
			createGroupAction() {
				var self = this;
				if(self.subject == '') {
				    console.log('", "')
					$.alert("", "");
					return;
				}
				var param = {
					subject: self.subject,
					description: self.description,
					members: self.members,
					optionsPublic: self.optionsPublic,
					optionsModerate: self.optionsModerate,
					optionsMembersOnly: self.optionsMembersOnly,
					optionsAllowInvites: self.optionsAllowInvites
				};

				httpTool.createGroup(param);
			},
		},
		mounted() {

		},
		destroyed() {

		},
	}
</script>

<style>

</style>


